@import "../../../../css/module/color";
@import "../../../../css/module/fn";

aside {
	position: fixed;
	background-color: #fff;
	border: 1px solid #ccc;
	box-shadow: 0 0 1px rgba(0, 0, 0, 0.1);
	
	&#left {
		top: 10px;
		left: 10px;
		width: 450px;
		bottom: 10px;
		
		.announcement {
			margin: 20px 12px;
			
			> h1 {
				color: $red;
				text-transform: uppercase;
				font-size: 20px;
				line-height: 48px;
			}
			#an1 {
				> h2 {
					
					line-height: 28px;
					margin-top: 1em;
					> a {
						font-size: 16px;
					}
				}
				> time {
					color: #aaa;
					font-size: 12px;
				}
				> .anContent {
					font-size: 14px;
					margin: 0.5em auto 2em;
					@include lineClip(2);
				}
			}
			
			#aMore {
				color: $red;
				float: right;
			}
		}
		
		.swiper-container {
			width: 448px;
			height: 240px;
			
			.swiper-slide {
				text-align: center;
				font-size: 18px;
				background: #fff;
				/* Center slide text vertically */
				display: -webkit-box;
				display: -ms-flexbox;
				display: -webkit-flex;
				display: flex;
				-webkit-box-pack: center;
				-ms-flex-pack: center;
				-webkit-justify-content: center;
				justify-content: center;
				-webkit-box-align: center;
				-ms-flex-align: center;
				-webkit-align-items: center;
				align-items: center;
				
				img {
					width: 448px;
					height: 240px;
				}
			}
			
		}
		
	}
	&#rightTop {
		position: static;
		width: 100%;
		height: 100%;
		
	}
	&#rightBottom {
		top: 50%;
		left: 470px;
		right: 10px;
		bottom: 10px;
	}
}

.rightTopWrap {
	position: fixed;
	top: 10px;
	left: 470px;
	right: 10px;
	bottom: 50%;
	background-color: transparent;
	border: none;
	padding-bottom: 10px;
}

